//variable
@import "variables";

//mixins
@import "mixins";
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.canvas {
    width: 600px;
    height: 400px;
    background-color: #92a8d1;
    position: relative;
    overflow: hidden;
}
.floor {
    width: 200%;
    height: 64px;
    background-image: url("../images/wall.png");
    position: absolute;
    bottom: 0;

    animation-name: floorAni;
    animation-duration: 5s;
    animation-delay: 0;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    @keyframes floorAni {
        0% {
	        left: 0;
        }
        100% {
	        left: -320px;
        }
    }
}
.plant {
    z-index: 1;
    width: 136px;
    height: 40px;
    background-image: url("../images/plant.png");
    position: absolute;
    bottom: 64px;

    animation-name: plantAni;
    animation-duration: 15s;
    animation-delay: 0;
    animation-iteration-count: infinite;
    animation-timing-function: linear;

}

@keyframes plantAni {
    0% {
        left: 600px;
    }
    100% {
        left: -136px;
    }
}

.plant2 {
    height: 32px;
    z-index: 0;
    animation-duration: 25s;
    animation-delay: 8s;
    filter: blur(1px);
}
.plant3 {
    height: 30px;
    z-index: 0;
    animation-duration: 25s;
    animation-delay: 16s;
    filter: blur(2px);

}
.plant4 {
    height: 35px;
    z-index: 0;
    animation-duration: 25s;
    animation-delay: 30s;
}
.pipe {
    z-index: 2;
    width: 64px;
    height: 128px;
    background-image: url("../images/pipe.png");
    position: absolute;
    bottom: 64px;

    animation-name: pipeAni;
    animation-duration: 12s;
    animation-delay: 0s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;

}

@keyframes pipeAni {
    0% {
        left: 600px;
    }
    100% {
        left: -64px;
    }
}
.cloud {
    z-index: 2;
    width: 80px;
    height: 60px;
    background-image: url("../images/cloud.png");
    position: absolute;
    top: 80px;

    animation-name: cloudAni;
    animation-duration: 5s;
    animation-delay: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;

}
.cloud1 {
    filter: blur(1px);
    z-index: 0;
    top: 35px;
    animation-duration: 20s;
}
.cloud2 {
    z-index: 1;
    top: 50px;
    animation-delay: 20s;
}
@keyframes cloudAni {
    0% {
        left: 600px;
    }
    100% {
        left: -64px;
    }
}
.mario {
    z-index: 30;
    width: 45px;
    height: 65px;
    background-image: url("../images/mario.png");
    position: absolute;
    bottom: 55px;
    left: 240px;

    /*animation-name: marioAni;
    animation-duration: 0.3s;
    animation-delay: 0s;
    animation-iteration-count: infinite;
    animation-timing-function: steps(20);*/

    animation: marioAni 0.2s 0s infinite steps(20),
                marioJump 1.2s 6s 1;
}

@keyframes marioAni {
    100% {
        background-position: -900px 0;
    }
}
@keyframes marioJump {
    0% {
        bottom: 55px;
    }
    30% {
        bottom: 220px;
    }
    50% {
        bottom: 180px;
    }
    70% {
        bottom: 220px;
    }
    100% {
        bottom: 55px;
    }
}
